const superagent = require('superagent');

//before using  db the flow was:
//component calls action generator
//action generator returns object
//component dispatches object
//redux store changes


// ADD_CONTENT

export const addContent = ( content  = '' ) => ({
    type: 'ADD_CONTENT',
    content
});

export const startAddContent = (content) => { 
    return (dispatch, getState) => {     
                             
        //const uid = getState().auth.uid;
        
        console.log('sent content', content);
         
        return superagent
            .post('/link.json')
            .send({content})
            .set('Content-Type', 'application/json')
            .end((err, res) => {
                if (err) { console.log('error from server: ', err);
                }
                console.log('startAddContent, superagent res.body: ', res.body.url);
                const link = res.body.url
                dispatch(addContent({
                 content, 
                 link 
                }));
                console.log('response from server: ', res);
            })

        //by returning the promise chain, we can continue chaining on over at expenses.test.js
        // return database.ref(`users/${uid}/expenses`).push(expense).then((ref) => {
        //     dispatch(addExpense({
        //         id: ref.key,
        //         ...expense
        //     }));
        // });
    };
};



