import React from 'react';
import { connect } from 'react-redux';

export const LinkDisplay = (props) => {
    
    let textContent = null

    const copyCodeToClipboard = async (e) => {
        window.getSelection().removeAllRanges();
        var range = document.createRange();
        range.selectNode(textContent);
        window.getSelection().addRange(range);
        document.execCommand("copy");
        window.getSelection().removeAllRanges();
      }
    
    const initRef = c => { textContent = c}

    const url = props.content.link? process.env.LINK_BASE_ADDRESS + '/link/'+  props.content.link : '';

    return (
        <div className="section">
            <div className="container">
                <div className="field has-addons">
                    <p ref={initRef} className="input is-primary">{url}</p>
                    
                    <p className="control">
                        <button className="button is-primary" onClick={copyCodeToClipboard}>
                            <span className="icon">
                                <i className="fas fa-copy"></i>
                            </span>
                        </button>
                    </p>
                </div>
            </div>
        </div>
        );
};

const mapStateToProps = (state) => {
    return {content: state.contents}
}

export default connect(mapStateToProps)(LinkDisplay);


