//Contents Reducer

const contentsReducerDefaultState = {};

//use a default export since there is  one thing to export is the reducer function
export default  (state = contentsReducerDefaultState, action) => {
    console.log('reducer, action, state: ', action, state);
    switch (action.type) {
        case 'ADD_CONTENT':
            return action.content;
        case 'REMOVE_CONTENT':
            return {};
        case 'EDIT_CONTENT':
            return {}
        default:
            return state;
    }
};


