import React from 'react';
import { connect } from 'react-redux';
import { startAddContent} from '../actions/contents';
import LinkDisplay from './LinkDisplay';


export class AddLinkPage extends React.Component {

    constructor(props) {
        super(props);

        console.log('AddLinkPage props: ', props);
        

        this.state={
            content: props.content.content,
            link: props.content.link || 'foo'
        }
    }
        onContentChange = (e) => {
            const content = e.target.value;
            this.setState(() => ({content}));
            console.log('AddLinkPage - onContentChange - content: ', content);
            console.log('AddLinkPage - onContentChange - this.state: ', this.state);
        };

        onSubmit = (e) => {
            e.preventDefault(); 
            console.log('AddLinkPage - onSubmit - this.state.content this.state.link: ', this.state.content, this.state.link);
            this.props.startAddContent(this.state.content);
            
        }
    
        render() {
            return (
                <div>
                    <form className="container" onSubmit={this.onSubmit}>
                        <div className="field">
                            <label className="label">Link Content:</label>
                            <div className="control">
                                <textarea 
                                    type="text"
                                    placeholder="Content"
                                    autoFocus
                                    className="textarea is-primary"
                                    rows="20"
                                    value={this.state.content}
                                    onChange={this.onContentChange}
                                />
                            </div>
                        </div>
                        <div className="field is-grouped is-grouped-right">
                            <div className="control"> 
                                <button className="button is-primary">Save</button>  
                            </div>
                        </div>
                    </form>
                    
                    <LinkDisplay />
                
                </div>  
             )
         };
    };
     
    

const mapStateToProps = (state, props) => ({
    content: state.contents
});
 //connecting the dispatch call to addContent, from outside to the AddLinkPage for testing purpose
 const mapDispatchToProps = (dispatch) => ({
    startAddContent: (content) => dispatch(startAddContent(content))
 });

 export default connect(mapStateToProps, mapDispatchToProps)(AddLinkPage);




